@media only screen and (max-width: 800px) {
    .logoTxt {
        display: none;
    }

    .navbaritems {
        display: none !important;
    }

    .navbaritems.active {
        display: block !important;
    }

    .toggle {
        display: block;
        margin-left: auto;
        margin-top: auto;
        margin-bottom: auto;
    }

    .toggle.active {
        margin-right: auto;
        margin-left: auto;
        transition: 0.3s;

    }

    .toggle:before {
        content: '\f0c9';
        font-family: fontAwesome;
        line-height: 40px;
        color: white;
        font-size: 32px;
    }

    .toggle.active:before {
        content: '\f00d';
        font-family: fontAwesome;
        line-height: 40px;
        color: white;
        font-size: 32px;
    }

    .navbaritems.active li {
        margin-top: 16px;
        transition: 0.3s;

    }

    .logoPhoto.active {
        height: 128px;
        transition: 0.3s;
    }

    #header .navbar ul li:nth-child(6) {
        border: none !important;
    }

    .index .floatingplane {
        width: 256px;
        position: absolute;
        top: 420px;
        left: 15%;
    }

    .about .sec1 {
        display: block;
    }

    .about .sec1 .text, .about .sec1 .img {
        width: 100%;
    }

    .about .sec1 .text h2 {
        font-size: 8vw;
    }

    .about .sec1 .text p {
        font-size: 4vw;
    }

    .about .sec2 {
        display: block;
    }

    .about .sec2 .text, .about .sec2 .img {
        width: 100%;
    }

    .about .sec2 .text h2 {
        font-size: 8vw;
    }

    .about .sec2 .text p {
        font-size: 4vw;
    }

    .about .sec3 .ul {
        width: 100%;
    }

    .indextext h1 {
        font-size: 32px;
    }

    .indextext p {
        font-size: 24px;
    }

    .navbar ul li a {
        font-size: 16px;
    }
    
    .faq .quests .quest {
        width: 80%;
    }
    
    .media .boxes {
        width: 90%;
    }
    .contactform {
        width: 100%;
    }

    .contactform input {
        margin-top: 32px;
    }

    .contactform button {
        width: 50%;
    }



}