@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
body {
    overflow-x: hidden;
    position: relative;
    font-family: 'Montserrat';
    /*cursor: url('/public/assets/mcursor.png'), auto; */
}

.bgImage {
    height: 100vh;
}

.header {
    z-index: 999;
    margin-top: 0;
    display: flex;
    width: 100vw;
    position: fixed;
    padding: 2vh 4vw;
}

@font-face {
    font-family: logofont;
    src: url(../fonts/logofont.ttf);
}

.logoWrapper {
    font-family: logofont;
    display: flex;
    align-items: center;
}

.logoImg, .logoTxt {
    display: flex;
    flex-direction: column;
}

.logoPhoto {
    height: 64px;
    transition: 0.3s;

}

.logoTxt {
    font-size: 40px;
    color: white;
}

.navbar {
    padding: 16px;
    width: 50%;
    margin-left: auto;

}

.navbar ul {
    list-style: none;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.navbar ul li {
    transition: 0.3s;
    font-size: 18px;
    color: white;
}

.navbar ul li:nth-child(6) {
    border-right: 2px white solid;
    padding-right: 16px;
}

.navbar ul li a {
    text-decoration: none;
    color: white;
    font-size: 1vw;

}

#header {
    transition: 0.3s;
}

#header.sticky {
    background: #fff;
    transition: 0.3s;
}

#header.sticky .logoTxt, #header.sticky .navbar li a {
    color: #000 !important;
}

#header.sticky .navbar ul li:nth-child(6) {
    border-right: 2px #000 solid;
    padding-right: 16px;
}

#header.sticky .toggle:before {
    color: black;
}

.toggle {
    display: none;
    transition: 0.3s;

}


.index {
    margin-top: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    background: url('/public/assets/Background_Clean.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}

.index .floatingplane {
    width: 720px;
    position: absolute;
    top: 300px;
    left: 250px;
}

.indextext{
    text-align: center;
}
.indextext h1{
    padding-top: 35vh;
    font-family: logofont;
    color: white;
    font-size: 96px;
}
.indextext p {
    font-size: 32px;
    color: red;
}



.about {
    padding: 8% 0;
    padding-bottom: 0%;
    background-color: white;
    color: black;
    font-weight: 400;
}
.about .sec1{
    width: 70%;
    margin: auto;
    display: flex;
}
.about .sec1 .text{
    width: 50%;
}
.about .sec1 .img{
    width: 50%;
}
.about .sec1 .img img{
    width: 100%;
    height: auto;
}
.about .sec1 .text h2{
    font-size: 64px;
    font-weight: 600;
}
.about .sec1 .text p{
    font-size: 1vw;
}


.about .sec2{
    width: 70%;
    margin: auto;
    display: flex;
}
.about .sec2 .text{
    width: 50%;
    padding: 8px 24px;

}
.about .sec2 .img{
    width: 50%;
}
.about .sec2 .img img{
    width: 100%;
    height: auto;
}
.about .sec2 .text h2{
    font-size: 64px;
    font-weight: 600;
}
.about .sec2 .text p{
    margin-top: 24px;
    font-size: 1vw;
}
.about .sec2{
    margin-top: 128px;
}
.about .sec3{
    width: 70%;
    margin: auto;
    margin-top: 64px;

}
.about .sec3 .ul{
    width: 50%;
    margin-left: auto;
}
.about .sec3 .ul p{
    font-size: 32px;
    font-weight: 600;
}

.team{
    margin-top: 32px;
    background-color: black;
    color: white;
    padding: 2% 8%;
}
.team .pilots h2, .team .ground-crew h2{
    font-size: 40px;
    font-weight: 700;
}
.team .pilots h3, .team .crew h3{
    font-size: 24px;
}
.team .slider .slider-item{
    padding: 8px 24px;
    position: relative;
    cursor: pointer;
}
.team .slider .slider-item img{
    width: 100%;
    height: 300px;
    object-fit: cover;
}
.team .slider .slider-item h2{
    position: absolute;
    bottom: 48px;
    left: 32px;
}
.team .slider .slider-item h3{
    margin-top: 32px;
    padding-left: 32px;
    font-weight: 400;

}
.team .crew .col-lg-6{
    padding: 32px 32px;
    position: relative;
}
.team .crew .col-lg-6 img{
    width: 100%;
}
.team .crew .col-lg-6 h2{
    position: absolute;
    bottom: 64px;
    left: 64px;
}
.team .crew .col-lg-6 h3{
    margin-top: 32px;
    padding-left: 32px;
    font-weight: 400;

}
.team .pilotinfo{
    margin-top: 32px;
    margin-bottom: 96px;
}
.team .pilotinfo h2{
    font-size: 24px;
}
.faq {
    margin-top: 32px;
    text-align: center;
}

.faq .quests {
    text-align: left;
}

.faq .quests {
    background-color: #e6e6e6;
    margin-top: 64px;
    padding: 16px 32px;
    padding-bottom: 260px;

}

.faq .quests .quest h2 {
    font-size: 24px;
}

.faq .quests .quest {
    margin-top: 32px;
    border-bottom: 1px red solid;
    width: 40%;
}

.faq .quests .quest:nth-child(1), .faq .quests .quest:nth-child(3) {
    text-align: right;
    margin-left: auto;
}


.media {
    text-align: center;
    font-size: 64px;
    color: white;
    overflow: hidden;
}

.media h1 {
    font-size: 64px;
}


.medcontent {
    padding: 32px;
}

.media a:hover>i {
    color: red;
    transition: 0.3s;
}

.media i {
    transition: 0.3s;
}

.media .boxes {
    z-index: 4;
    padding-top: 64px;
    display: flex;
    width: 60%;
    justify-content: space-between;
    margin: auto;
}

.media .boxes .box a {
    color: white;
    text-decoration: none;
}

.media {
    transform: skew(0deg, -3deg) translateY(-200px);
}

.medbg {
    padding: 64px 16px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 600px;
    transform: skew(0deg, 3deg) translateY(-80px);

}
.medcontent{
    position: absolute;
    width: 100%;
    top: 64px;
}
.medbg img{
    width: 120%;
    position: absolute;
    top: 0;
    left: 0;
}


.contact {
    padding: 32px 32px;
    background: url('/public/assets/bg5.jpg');

}

.contact h1 {
    z-index: 4;
    font-size: 32px;
    margin-bottom: 32px;
    text-align: center;
}

.contact h3 {
    font-size: 24px;
}

.contactform {
    padding: 32px 32px;
    width: 60%;
    margin: auto;
    border: 1px red solid;
    text-align: center;
}

.contactform .row {
    margin-bottom: 16px;
}

.contactform input, .contactform textarea {
    width: 100%;
    padding: 8px;
}

.contactform button {
    width: 20%;
    margin: auto;
    padding: 8px;
    color: white;
    background: red;
    border: none;
    outline: none;
}

.apply {
    padding: 5% 5%;
    text-align: center;
    background: url('/public/assets/apply.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
}
.footer{
    text-align: center;
}